<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left users">
                <h2>Real name: {{ getUser.name }}</h2>
                <p> User name: {{ getUser.username }}</p>
                <p> Email: {{ getUser.email }}</p>
                <p> Street: {{ getUser.address.street }}</p>
                <p> City:{{ getUser.address.city }}</p>
                <p> geo:{{ getUser.address.geo.lat }}</p>
                <p> {{ getUser.address.geo.lng }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                profile: null,
                currentId: null
            }
        },
        created() {
            this.getUsers()
            this.currentId = this.$route.params.id
            this.profile = this.$store.state.users                
        },
        computed: {
            getUser() {
                return this.profile.find((user) => user.id == this.currentId)
            },  
            users(){
                return this.$store.state.users
            }
        },
        methods:{
            getUsers() {
                this.$store.dispatch('getUsers')
            },
        }
    }
</script>

